footer{
    position: relative;
    top: 100px;
    float: left;
    width: 100%;
    background-color:royalblue ;
    background-image: linear-gradient(to right, #a10fff,royalblue);
    color: #000a1a;
    text-align: center;
}
.footer-contents{
    width: 85%;
    margin: 0 auto;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
}
.go-social{
    width:30%;
    padding: 5%;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;
}
.go-social h2{
    display: block;
    width: 100%;
    margin: 0 auto;
}

.copyright-desclimer{
    width: 70%;
    padding: 5%;
    text-align: left;
    font-size: smaller;
    color: #d9d9d9;
}
.go-social i{
    display: flex;
    align-items: center;
    justify-content: center;
    /* margin: 5%; */
    color: #33b457;
    background-color: #000a1a;
    width: 40px;
    height: 40px;
    border-radius: 50%;
}
.go-social i:hover{
    background-color: #d9d9d9;
}
#copyright{
    background-color:#000a3a;
    margin: 0;
    padding:2%;
    color:#d9d9d9;
}

@media screen and (max-width:913px){
    .go-social{
        width: 100%;
        margin-top: 5%;
    }
    .copyright-desclimer{
        width: 100%;
    }
    #copyright{
        margin: 40px;
    }
}