.navbar {
    width: 100%;
    height: auto;
    background-image: linear-gradient(rgb(0, 51, 128), rgb(0, 10, 26));
    color: rgb(242, 242, 242);
    border-bottom: 1px solid rgb(0, 51, 128);
    position: fixed;
    z-index: 1;
}

.topnav {
    overflow: hidden;
    width: 85%;
    margin: 0px auto;
    text-align: right;
}

.topnav a {
    margin-top: 15px;
    float: right;
    color: rgb(242, 242, 242);
    text-align: center;
    padding: 0px 16px;
    text-decoration: none;
    font-size: 17px;
}

.topnav .logo {
    margin-top: 0px;
    float: left;
}

.topnav a:hover {
    color: rgb(161, 0, 255);
}

.topnav a.active {
    color: rgb(161, 0, 255);
}

.topnav .icon {
    background-image: linear-gradient(to right, blue, rgb(161, 0, 177));
    display: none;
}

@media screen and (max-width: 768px) {
    .topnav a:not(:first-child) {
        display: none;
    }

    .topnav button.icon {
        color: rgb(242, 242, 242);
        float: right;
        display: block;
        width: 50px;
        height: 40px;
        border: none;
        margin: 5px;
        border-radius: 5px;
    }
}

@media screen and (max-width: 768px) {
    .topnav.responsive {
        position: relative;
    }

    .topnav.responsive .icon {
        position: absolute;
        right: 0px;
        top: 0px;
    }

    .topnav.responsive a {
        float: none;
        display: block;
        text-align: left;
    }

    a:not(:first-child) {
        margin: 15px;
    }

    .topnav {
        padding-left: 0px;
    }

    .logo img {
        width: 170px;
    }
}

@media screen and (max-width: 600px) {
    .topnav {
        width: 100%;
    }

    .topnav .logo {
        padding-left: 5px;
    }

    .logo img {
        width: 150px;
        margin-top: 4px;
    }
}