.main-container-tech{
    position: relative;
    top: 100px;
    width: 100%;
    border-top: 1px solid #003380;
    border-bottom: 1px solid #003380;
    background-image: linear-gradient(black, #003380, black);
    padding-bottom: 5%;
}

.tech-title-container{
    width: 85%;
    margin: 0 auto;
    text-align: center;
    padding: 3rem 0.3rem 1rem;
    color: #b1b1b1;
    width: 50%;
}
.tech-title-container h3{
    margin: 40px auto 0;
    font-weight:500;
    color: #d9d9d9;
    font-size: 1.7rem;
    line-height: 2.3rem;
}

.tech-items-container{
    width: 85%;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;
    margin: 0 auto;
    padding: 5%;
}

.tech-item{
    width: 70px;
    padding: 0.5%;
    margin: 10px;
    background-color: white;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    border-radius: 5px;
    
}
.tech-item:hover{
    cursor:pointer;
    background-color: #f2f2f2;
    box-shadow: 3px 3px 60px 5px lightblue;
}
.tech-item h3{
    display: none;
}

.tech-item:hover h3{
    color: #eaa10c;
}

.tech-item img{
    width: 100%;
    height: auto;
}

@media only screen and (max-width:600px){
    .tech-item {
        width: 5rem;
        padding: 2% 4%;
    }
    .tech-item h3{
        font-size: small;
    }
    .tech-title-container{
        width: 95%;
    }
    .tech-items-container{
        width: 95%;
    }
}