#contact {
    /* background-color: #003399;*/
    /*background-color: #cc8500;*/
    /* background-image: linear-gradient(to right, blue, #A100ff); */
    text-align: center;
    padding: 5%;
    padding-bottom: 10%;
    position: relative;
    top: 100px;
}

.consult-me{
    display: block;
    
    font-weight: 700;
    margin-bottom: 30px;
    text-shadow: 5px 5px 8px rgba(0,0,0,0.2);
}

.consult-me-desc {
    font-size: 1.5rem;
    font-weight: 700;
    /* border: 1px solid #ffaf1a; */
    padding: 10px;
    color: #f2f2f2;
    background-color: #003380;
    background-image: linear-gradient(to right, #A100FF, #3860FF);
    line-height: 3rem;
    box-shadow: 5px 5px 8px rgba(0,0,0,0.2);
}

.contact-container-main {
    border: 1px solid #A100FF;
    background-color: #001B45;
    border-radius: 15px;
    padding: 1%;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;
    width: 85%;
    margin: 0 auto;
    margin-top: 40px;
}

.form-container {
    border-radius: 15px;
    width: 50%;
    padding: 1%;
    padding-right: 2%;
    text-align: left;
    display: flex;
    align-items: center;
}

form{
    width: 100%;
}

.form-img-container {
    width: 50%;
    display: flex;
    align-items: center;
    justify-content: center;

}
.form-img-container img{
    max-width: 80%;
}

.form-container label {
    color: #f2f2f2;
    margin: 10px;
    margin-bottom: 10px;
    display: block;
}


.form-container input,textarea {
    width: 98%;
    font-size: 1rem;
    padding: 10px;
    border: none;
    border-radius: 15px;
    box-shadow: 0 5px 5px rgba(0, 0, 0, 0.1);
    margin: 10px;
    margin-top: 0;
}

.form-container textarea{
    height: 100px;
}
select{
    width: 98%;
    margin:0 10px 10px;
    padding: 10px;
}

.form-img-container img {
    width: 100%;
    height: auto;
}

.btn-book{
    margin: 30px auto 30px;
}
.btn-container{
    display: flex;
    align-items: center;
    justify-content: center;
}

.hire-me-container{
    width: 50%;
    margin: 5% auto 0;
    border: 1px solid #33b457;
    padding: 0 5px;
}
.lets-build-something{
    display: block;
    color: #d9d9d9;
    font-size: 150%;
    font-weight: 500;
}


@media only screen and (max-width:960px) {
    .contact-container-main {
        width: 100%;
    }

    .form-container{
        width: 100%;
    }
    .form-img-container{
        width: 100%;
    }

    form{
        width: 96%;
    }
    form label{
        font-size: small;
    }
    .hire-me-container{
        width: 100%;
        margin: 20% 0;
    }
}