
.main-container{
    position: relative;
    top: 100px;
    width:85%;
    margin:50px auto;
    background-color: #000a4a;
    padding: 5% 0;
    border: #003380 1px solid;
}
.main-container:hover{
    background-color:#000a5a ;
}

.title-container{
    display: block;
    padding: 0 5% 5%;
    /* background-image: linear-gradient( #000a1a, #653456); */
}
.h3{
    margin-top: 2rem;
    color: #b1b1b1;
    font-weight: 400;
}
.title-text {
    width: 70%;
    text-align: center;
    margin: 0 auto;
    font-size: 1.7rem;
    color: #d9d9d9;
    font-weight: 500;
}


.service-desc{
    width:100%;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;
    padding: 0 3%;
    /* background-image: linear-gradient( #653456, #000a1a); */
    
}
.service-item{
    width: 15rem;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    text-align: center;
    border-radius: 15px;
    padding: 2%;
    margin: 10px;
    background-color: #003380;
    border: 1px solid #003380;
}
.service-item img{
    width: 40%;
}
.service-item h3{
    color: #b3b3b3;
    font-weight: 400;
}
.service-item:hover{
    cursor: pointer;

    background-color: #003370;
    }

.service-item:hover .h3{
    color: #f2f2f2;
}

.service-item:hover img{
    transform:rotateZ(5deg);
}


@media only screen and (max-width:600px){
    .title-container{
        padding: 0 0 10%;
    }
    .h3{
        font-size: medium;
    }
    .title-text{
        color: #d9d9d9;
        width: 90%;
        font-size: 1.3rem;
    }
    .main-container{
        width: 95%;
    }
}
