#qualifications{
    position: relative;
    top:100px;
    background-color: #0094FF;
    width: 100%;
    padding: 50px 0;
}
.qualifications-container{
    width:85%;
    margin: 0 auto;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    align-items: flex-start;
    border: 1px solid black;
    background-color: white;
    box-shadow: 5px 5px 10px 1px rgba(0,0,0,0.2);
    border-radius: 15px;
}
.qualifications-desc{
    width: 50%;
    padding: 2%;
}

.qualifications-desc p{
    font-weight: 600;
    font-size: 1.7rem;
    line-height: 3.4rem;
}

.qualifications-list{
    width:50%
}

#badge{
    width: 120px;
    height: auto;
}
.special-text{
    font-size: 3rem;
    display: block;
    font-weight: 700;
    margin-bottom: 40px;
    color: #a100ff;
    background-image: linear-gradient(to right,  #0094FF,#33b457);
    background-size: 100%;
    background-clip: text;
    -webkit-background-clip: text;
    -moz-background-clip: text;
    -webkit-text-fill-color: transparent; 
    -moz-text-fill-color: transparent;
}
.qualifications-list{
    padding: 2%;
}
.qualification{
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    align-items: center;
    background-color: white;
    margin-bottom: 20px;
    border-radius: 5px;
    box-shadow: 3px 3px 5px 1px rgba(0,0,0,0.2);
}

.qualification:hover{
    background-color: #f1f1f1;
}

.institute-logo {
    padding: 2%;
    width: 15%;
}
.institute-logo img{
    width: 100%;
}
.credentials{
    width: 85%;
    padding: 2%;
}
.credentials h3{
    font-size: 1rem;
    font-weight: 500;
    margin: 0;
    opacity: 0.9;
    margin-bottom: 3px;
}
.credentials a{
    display: block;
    font-size: small;
    margin: 0;
    margin-top: 5px;
}
.credentials span{
    color: gray;
    font-size: small;
}
.credentials p{
    margin: 0;
    color: rgba(0,0,0,0.7);
    font-size: small;
}
.qualifications-header{
    display: flex;
    flex-direction: row;
    align-items: center;
    column-gap: 10px;
    justify-content: center;
    margin-bottom: 30px;
}
.qualifications-header i{
    font-size: 2.5rem;
    color: #a100ff;
    background-image: linear-gradient(to right, orange,bisque);
    background-size: 100%;
    background-clip: text;
    -webkit-background-clip: text;
    -moz-background-clip: text;
    -webkit-text-fill-color: transparent; 
    -moz-text-fill-color: transparent;
}

@media only screen and (max-width:772px) {
    .qualifications-container{
        width: 95%;
    }
    .qualifications-desc{
        width: 95%;
    }
    .qualifications-list{
        width:100%;
    }
    .special-text{
        text-align: left;
        font-size: 2.6rem;
        line-height: 2.8rem;
        width: 95%;
        margin: 40px auto 20px;
    }
    .qualifications-desc p{
        font-size: large;
        line-height:2.7rem;
        width: 95%;
        margin: 0 auto 40px;
        text-align: left;
    }
}

