.portfolio-section{
    position: relative;
    top: 100px;
    background-color: #00004d;
    background-color: #50c878;
    background-image: radial-gradient( #a100ff,#001B45);
    margin-top:0 ;
}
.main-container-portfolio{
    width: 85%;
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    margin: 50px auto;
    overflow-x: auto;
    border-radius: 15px;
    padding: 2%;
    border: 1px solid skyblue;
}
.main-container-portfolio::-webkit-scrollbar{
    display: none;
}

.carousel-item-container{
    display: flex;
    flex-direction: row;
}


.carousel-nav-button{
    width: 30px;
    height: 30px;
    padding: 1%;
    background-color: #00334d;
    border: 1px solid #1a8cff;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 5px;
}
.carousel-nav-button:hover{
    background-image: linear-gradient(to right,#1a8cff,#8800cc);
    cursor: pointer;
}
.carousel-nav-button img{
    width: 15px;
    height: auto;
}

.carousel-wraper{
    display: flex;
    align-items: center;
    flex-direction: row;
}

.prev{
    position: relative;
    float: left;
    margin-left: 1%;
}
.next{
    position: relative;
    float: right;
    margin-right: 1%;
}


.project-name{
    font-size: 1.2rem;
    color: black;
    font-weight: 600;
}
.project-desc{
    color: #404040;
}

.tech-icons-container{
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: row;
    margin: 15px auto;
}
.used-tech-img{
    width: 45px;
    height: auto;
    margin: 5px;
}
.carousel-item{
    width: calc(380px);
    height: auto;
    background-color:white;
    margin: 20px;
}
.carousel-img-container{
    padding: 5px;
    width: 100%;
    /*border-bottom: 5px solid #00004d;*/
}
.carousel-text-container{
    padding: 5%;
    display: flex;
    flex-direction: column;
    justify-content: center;
}
.carousel-text-container ul{
    list-style: none;
    line-height: 1.5rem;
    font-size: small;   
}

.carousel-text-container i{
    color: #33b457;
    margin-right: 5px;
}

.project-image{
    width: 100%;
    height: 170px;
    object-fit: cover;
    border-radius: 15px;
    padding: 5px;
}

.portfolio-title{
    position: relative;
    margin: 0;
    text-align: center;
    padding: 50px;
    padding-bottom: 20px;
}
.portfolio-title h2{
    margin: 0;
    display: inline-block;
   
}
.carousel-item button{
    margin: 5px;
}

/*mobile*/
@media only screen and (max-width:600px){
    .main-container-portfolio{
        padding: 0;
        margin-top: 20px;
        /*width: 100vw;*/
    }

    .carousel-item{
        width: calc(100vw/1.21);
        height: auto;
        background-color:white;
        border-radius: 15px;
        border: 1px solid #003380;
        transition: 0.5ms ;
        margin: 0 ;
        margin-right: calc(100vw/8);
    }
    .carousel-nav-button{
        width: 30px;
        height: 100px;
    }
}