h1{
    margin: 0;
    color: #0099e6;
    font-weight: 600;
    text-align: left;
}

.name{
    text-align: left;
    font-family: 'Times New Roman', Times, serif;
    font-size: 3rem;
    font-weight: 700;
    display: block;
    margin-bottom: 2rem;
    color: #ffaf1a;
    color: #33B457;
    /* background-image: linear-gradient(to right,  #0094FF,#50c878);
    background-size: 100%;
    background-clip: text;
    -webkit-background-clip: text;
    -moz-background-clip: text;
    -webkit-text-fill-color: transparent; 
    -moz-text-fill-color: transparent; */

    /*oc ==> "#A100FF" "#0094FF" "#50C878"*/
}

.hero-container{
    position:relative;
    top: 100px;
    width: 85%;
    margin: 0 auto;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center; 
}
.text-container{
    padding: 5%;
    width: 50%;
}

.text-container h1,h3{
    margin-bottom: 5%;
}


.image-container{
    padding: 5%;
    width: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
}
.hero-img{
    width: 80%;
    height: auto;
    transform: scaleX(-1);
}

button{
    margin: 4%;
    margin-left: 0;
    padding: 10px 20px;
    background-color: #00334d;
    color: #00ccff;
    border: 1px solid #1a8cff;
    border-radius: 30px;
    box-shadow: 0 5px 5px rgba(0 0 0 0.2);
    font-weight: 600;
    font-size: 1rem;
    cursor: pointer;
}

button:hover{
    background-color: #26e077;
    background-image: linear-gradient(to right, #1a8cff,#8800cc);
    box-shadow: 3px 3px 50px 3px #00ccff;
    color: #f2f2f2;
}


@media only screen and (max-width:912px) {
    .text-container{
        width: 100%;
    }
    
    .image-container{
        width: 100%;
    }
    .image-container img{
        width: 75%;
    }
    
     button{
        font-size: small;
     }
}
@media only screen and (max-width:600px){
    h1{
        font-size: 1.4rem;
        margin-top: 2rem;
     }
     .name{
         font-size: 2.6rem;
     }
     .text-container{
        padding: 2%;
        text-align: left;
     }
     .hero-img{
        width: 100%;
     }
     .image-container{
        padding: 0;
        margin-top: 20px;
     }
} 

